.chat_message_box {
  background-color: #ffffff;
  height: 62vh;
  background: url("https://zefyron-assets.fra1.digitaloceanspaces.com/Logo-Zefyron/zefyron_emblem.svg")
    no-repeat 50% 50%;
}
.message01_search_input::placeholder {
  color: #000000;
  font-family: "Nunito";
  font-weight: 600;
}
.recentChats01_block {
  max-height: 65vh;
  overflow-y: scroll;
  width: 280px;
}
/* .recentChats01_block::-webkit-scrollbar-track  {
  display: none;
  } */

.message_send_label {
  width: 100%;
  position: relative;
}
.message_send_label .fab-container {
  right: 100px;
  bottom: 8.9vh;
  margin: 0;
}
.message_send_label .fab-item {
  box-shadow: none !important;
  background-color: #31b9a3 !important;
}
.message_send_label .fab-rotate {
  background-color: transparent !important;
  margin-bottom: -12px !important;
}
.message_send_label .fab-item label svg {
  margin-top: 7px;
}

.message_send_label label {
  cursor: pointer;
}
.messages_root .partner_chats_input_block {
  border-radius: 12px;
}

.message_btn {
  position: absolute;
}

.chat_input {
  position: relative;
  width: 100%;
  height: 65px;
  font-family: "Nunito";
  border-radius: 5px;
  padding: 0px 150px 0 10px;
  border: none;
  line-height: 5;
  word-break: break-all;
}

.attach_btn {
  background: transparent;
  border: none;
  right: 0;
  top: 8px;
  height: 50px;
  width: 50px;
}

.video_call_btn {
  right: 150px !important;
}

.send_btn {
  background: #000000;
  border: none;
  color: #ffffff;
  right: 12px;
  top: 8px;
  height: 50px;
  font-family: "Nunito";
  border-radius: 5px;
  padding: 12px 24px;
}

.send_btn_img {
  width: 20px;
}

.no_chat_svg {
  fill: #9f9f9f;
}

.no_chat_block {
  margin-top: 18%;
}

.chatmessage {
  margin: 10px 0;
}

.chatmessage p {
  width: auto;
  margin: 0;
}

.self_message {
  flex-direction: row-reverse;
}

.mesage_text {
  /* margin: 0 10px; */
  padding: 8px;
}

.self_message .mesage_text {
  /* background-color: #e5e5e5; */

  background: rgba(201, 240, 255, 0.2);
  padding: 5px 10px;
  border-radius: 5px;
}

.other_message .mesage_text {
  background: rgba(201, 240, 255, 0.2);

  padding: 5px 10px;
  border-radius: 5px;
}

.self_message .chat_time {
  text-align: right;
}

.other_message .chat_time {
  text-align: left;
}
.chat_time {
  margin: 5px 0 0 !important;
  color: #afafaf;
}

.sendFileMsgMain {
  width: 50px;
  display: inline-flex;
  position: absolute;
  right: 110px;
  height: 100%;
  cursor: pointer;
}

.sendFileMsgMain label {
  position: absolute;
  left: 0;
  right: 0;
  height: 100%;
}

.sendFileMsgMain label button {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  cursor: pointer;
  bottom: 0;
  /* z-index: 10; */
}

.sendFileMsgMain input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  width: 0;
  top: 0;
  left: 0;
}

.sendFileMsgMain label figure {
  width: 100%;
  height: 100%;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.deletemsg {
  opacity: 0;
}
.deletemsg button .MuiSvgIcon-root {
  fill: #9f9f9f;
}
.mdmsg:hover .deletemsg {
  opacity: 1;
}

.mdmsg:hover .deletemsg svg {
  cursor: pointer;
}

.deletemsg button {
  border: none;
  background: transparent;
  cursor: pointer;
  outline: none;
}

.message_doc-main {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #b9b9b9;
  padding: 10px;
  border-radius: 5px;
  border-top-right-radius: 8% 37%;
}

.doc_download_main button {
  background: #45cbc2;
  color: white;
  padding: 10px;
  border-radius: 50%;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.doc-name-type-main {
  margin-left: 20px;
  margin-right: 10px;
}

.doc-name-type-main p {
  margin: 0;
  color: white;
  text-transform: capitalize;
  font-family: "Nunito";
  color: black;
}

p.docname {
  font-size: 18px;
  font-weight: 600;
}

p.doctype {
  font-size: 14px;
}

.time-type {
  display: flex;
  justify-content: right;
}

.allfilesIconmain {
  position: absolute;
  bottom: 105px;
  right: 104px;
  padding: 0;
  border-radius: 5px;
}

.hideFileIcons,
.hideFile {
  width: 0;
  height: 0;
  transition: 0.3s ease-in-out;
  opacity: 0;
  display: none;
  /* animation: hideIcon 0.3s ease-out; */
  /* display: none; */
}

.showFileIcon {
  display: inline-block;
  width: -moz-fit-content;
  width: fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.showFile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 0.3s ease-in-out;
  animation: showIcon 0.3s ease-in-out;
  width: 100%;
}

@keyframes showIcon {
  0% {
    height: 70px;
  }

  25% {
    height: 80px;
  }

  50% {
    height: 120px;
  }

  75% {
    height: 160px;
  }

  100% {
    height: 100%;
  }
}

.filesmain figure {
  margin: 0;
}

.filesmain svg {
  color: white;
  font-size: 25px;
}

