.filesmain {
  margin-bottom: 10px;
}
.filesmain figure {
  cursor: pointer;
}

.attach_btn figure {
  margin: 0;
}

.showBlur {
  background: #e3e1e7;
  border-radius: 50%;
  padding: 7px;
  transition: 0.3s ease-in-out;
}

.hideBlur {
  background: transparent;
  border-radius: 0;
  padding: 0px;
  transition: 0.3s ease-in-out;
}

.filesmain input {
  /* width: 100%; */
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  cursor: pointer;
  width: 0;
}
.message_attachment_popper {
  background: white;
  padding: 5px;
}
.message_attachment_popper svg {
  background: linear-gradient(
    90deg,
    rgba(104, 193, 210, 1) 0%,
    rgba(43, 210, 182, 1) 100%
  );
  border-radius: 50%;
  color: #fff;
  padding: 5px;
  height: 35px;
  width: 35px;
  margin: 7px;
  cursor: pointer;
}
.sendFileMsgMain .MuiButton-root {
  /* background: transparent; */
  box-shadow: none;
  min-width: 45px;
  margin-right: 10px;
}
.sendFileMsgMain .MuiButton-root:hover {
  /* background: transparent; */
  box-shadow: none;
}
.zef_invite_modal .MuiTabs-root {
  background-color: White;
  color: #000000;
}

.border-right {
  border-right: 1px solid black;
}

.zef_broadcast_message_input {
  height: 150px;
  width: 90%;
  margin: auto;
  border-radius: 8px;
  border: 1px solid #cacaca;
  padding: 5px 10px;
  font-family: Nunito;
}
.zef_broadcast_message_input::placeholder {
  color: #9f9f9f;
  font-family: Nunito;
}
.zef_broadcast_message .users_list {
  max-height: 110px;
  min-height: 110px;
  overflow-y: scroll;
  margin-bottom: 10px;
}
.chat01_select {
  position: relative;
}
.chat01_select_dropdown {
  position: absolute;
  background: #ffffff;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  width: 100%;
  z-index: 100;
  font-weight: 600;
  -webkit-box-shadow: 0px 4px 6px -3px rgba(94, 94, 94, 1);
  -moz-box-shadow: 0px 4px 6px -3px rgba(94, 94, 94, 1);
  box-shadow: 0px 4px 6px -3px rgba(94, 94, 94, 1);
}
.chat01_select_dropdown p {
  font-size: 14px;
  cursor: pointer;
  font-weight: 600;
  margin: 5px 8px;
  padding: 10px 5px;
}
.chat01_select_dropdown p:hover {
  background-color: rgba(169, 212, 217, 0.3) !important;
  border-radius: 5px;
}

.dashboard01_block {
  width: 600px;
